@import './fonts';
@import './variables';
@import './libs/normalize';

@import './components/all';

::-webkit-scrollbar {
  width: 0px;
  background: rgba(255, 255, 255, 0.0);
}

body {
 background-color: $background;
 padding: 50px 0px 0px 0px;
}

.wrapper {
 width: 95%;
 height: 100%;
 background-color: #fff;
 margin: 0px auto;
 border-radius: 10px;
 max-width: 1400px;
}

.content {
 padding: 40px 0;

 &__title {
  margin: 35px 0;
 }

 &__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
 }

 &__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
 }
}

.container {
 width: $container-width;
 margin: 0 auto;
overflow-y: hidden;

 &--cart {
  max-width: 820px;
  margin: 30px auto;
  .content__title {
   margin: 0;
  }
 }
}

.cart {
 &__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
 }

 .content__title {
  display: flex;
  align-items: center;
  font-size: 32px;

  svg {
   position: relative;
   top: -2px;
   width: 30px;
   height: 30px;
   margin-right: 10px;
   path {
    stroke: $black;
    stroke-width: 1.9;
   }
  }
 }

 &__clear {
  display: flex;
  align-items: center;
  cursor: pointer;
  @include noselect();

  span {
   display: inline-block;
   margin-left: 7px;
   color: #b6b6b6;
   font-size: 18px;
  }

  span,
  svg,
  path {
   transition: all $duration ease-in-out;
  }

  &:hover {
   svg {
    path {
     stroke: darken($color: #b6b6b6, $amount: 50);
    }
   }
   span {
    color: darken($color: #b6b6b6, $amount: 50);
   }
  }
 }

 &__item {
  display: flex;
  width: 100%;
  border-top: 1px solid $gray-line;
  padding-top: 30px;
  margin-top: 30px;

  &-img {
   display: flex;
   align-items: center;
   margin-right: 15px;
   width: 10%;

   img {
    width: 80px;
    height: 80px;
   }
  }

  &-info {
   display: flex;
   flex-direction: column;
   justify-content: center;
   width: 40%;

   h3 {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.01em;
   }

   p {
    font-size: 18px;
    color: #8d8d8d;
   }
  }

  &-count {
   display: flex;
   align-items: center;
   justify-content: space-between;
   width: 13%;

   &-minus {
    svg {
     path:first-of-type {
      display: none;
     }
    }
   }

   b {
    font-size: 22px;
   }
  }

  &-price {
   display: flex;
   align-items: center;
   justify-content: center;
   width: 33%;

   b {
    font-weight: bold;
    font-size: 22px;
    letter-spacing: 0.01em;
   }
  }

  &-remove {
   display: flex;
   align-items: center;
   justify-content: flex-end;
   width: 4%;

   .button {
    border-color: darken($color: $gray-line, $amount: 10);
   }

   svg {
    transform: rotate(45deg);

    path {
     fill: darken($color: $gray-line, $amount: 15);
    }
   }

   .button {
    svg {
     width: 11.5px;
     height: 11.5px;
     position: relative;
    }
    &:hover,
    &:active {
     border-color: darken($color: $gray-line, $amount: 80);
     background-color: darken($color: $gray-line, $amount: 80);
    }
   }
  }
 }

 &__bottom {
  margin: 50px 0;

  &-details {
   display: flex;
   justify-content: space-between;

   span {
    font-size: 22px;

    &:last-of-type {
     b {
      color: $orange;
     }
    }
   }
  }

  &-buttons {
   display: flex;
   justify-content: space-between;
   margin-top: 40px;

   .go-back-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 210px;

    border-color: darken($color: $gray-line, $amount: 10);

    span {
     color: darken($color: $gray-line, $amount: 20);
     font-weight: 500;
     font-weight: 600;
    }

    &:hover {
     background-color: darken($color: $gray-line, $amount: 90);
     border-color: darken($color: $gray-line, $amount: 90);

     span {
      color: $gray-line;
     }
    }

    svg {
     margin-right: 12px;
     path {
      fill: transparent;
      stroke-width: 2;
     }
    }
   }

   .pay-btn {
    font-size: 16px;
    font-weight: 600;
    width: 210px;
    padding: 16px;
   }
  }
 }

 &--empty {
  margin: 0 auto;
  width: 100%;
  text-align: center;

  h2 {
   font-size: 32px;
   margin-bottom: 10px;
  }

  p {
   font-size: 18px;
   line-height: 145.4%;
   letter-spacing: 0.01em;
   color: #777777;
  }

  icon {
   position: relative;
   top: 2px;
  }

  img {
   display: block;
   max-width: 50%;
   margin: 45px auto 60px;
  }

  .button--black {
   padding: 12px 0 14px;
   width: 230px;
   margin: 0 auto;
   font-weight: 600;
   font-size: 18px;
  }
 }
}

@media (max-width: 767px) {
  .content {
    &__top {
     display: block;
    }
    &__items {
      justify-content: center;
     }
   }
}